import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Header extends Component {
    render() {
        return (
        <React.Fragment basename={process.env.PUBLIC_URL}>
        <header className="templateux-navbar" data-aos="fade-down">
            <div className="container-fluid">
                <div className="row">
                <div className="col-sm-3 col-3"><div className="site-logo"><Link to="/" onClick={() => window.location.assign('/')} className="animsition-link">Portofolio</Link></div></div>
                <div className="col-sm-9 col-9 text-right">

                    <button className="hamburger hamburger--spin toggle-menu ml-auto js-toggle-menu" type="button">
                    <span className="hamburger-box">
                        <span className="hamburger-inner"></span>
                    </span>
                    </button>  

                    <nav className="templateux-menu js-templateux-menu" role="navigation">
                    <ul className="list-unstyled">
                        <li className="d-md-none d-block active"><a href="index.html" className="animsition-link">Home</a></li>
                        <li><Link to="work" onClick={() => window.location.assign('Portfolio-Nova-Azis/work')} className="animsition-link">Work</Link></li>
                        <li><Link to="about" onClick={() => window.location.assign('Portfolio-Nova-Azis/about')} className="animsition-link">About</Link></li>
                        <li><Link to="contact" onClick={() => window.location.assign('Portfolio-Nova-Azis/contact')} className="animsition-link">Contact</Link></li>
                    </ul>
                    </nav>  
                </div>
                </div>
            </div>
    </header>
    </React.Fragment>
        )
    }
}

export default Header;
